
import axios from 'axios';

  export default {
    props: ['avatar'],
    data: () => ({
      usernameEdit: "",
      error: false,
      linkavatar: "",
      intra_login: "",
      status: "",
      username: "",
      image: null,
      dialog: false,
      QRcode: "",
      verification: "",
      id: -1,
      twofactor: false
    }),
    methods: {
      disbale2fa(){
        const token = localStorage.getItem('token');
        if (token) {
          axios.get("/2FA/disable", {
            headers: {
                Authorization: token
              }
          }).then((function (res) {
            this.twofactor = false;
            axios.get('/QR', {
                headers: {
                  Authorization: token
              }}).then(res => {
                this.QRcode = res.data;
              })
              .catch((function (err) {
                this.error = true;
              }).bind(this));
          }).bind(this)).catch((function (err) {
            this.error = true;
          }).bind(this));
        }
      },
      loadImage(event) {
        const token = localStorage.getItem('token');
        const { files } = event.target;
        if (token && files && files[0]) {
          const data = new FormData();
          data.append("file", files[0]);
          
          axios.post("/upload/image", data, {
            headers: {
                Authorization: token
              }
          }).then((function (res) {
            this.error = false;
            this.$emit('changeAvatar', res.data);
          }).bind(this)).catch((function (err) {
            this.error = true;
          }).bind(this));
        }
      },
      updateusername: function()
      {
        if (this.usernameEdit && this.usernameEdit.length >= 5 && this.usernameEdit.length <= 10 && this.usernameEdit !== this.username)
        {
          const token = localStorage.getItem('token');

          if (token)
          {
            axios.patch('/update', {
              username: this.usernameEdit,
            }, {
              headers: {
                Authorization: token
              }
            }).then(res => {
              this.dialod = false;
              this.error = false;
              this.username = this.usernameEdit;
            })
            .catch((function (err) {
              this.error = true;
            }).bind(this));
          }
        }
        else
          this.error = true;
        if (this.verification.length == 6)
        {
          if (/^[0-9]+$/.test(this.verification))
          {
            const data = {
              id: this.id,
              number: +this.verification
            };
  
            axios.put('/2FA/verify', data, {}).then((function (res) {
              this.dialod = false;
              this.error = false;
              this.twofactor = true;
            }).bind(this))
            .catch((function (err) {
              this.error = true;
            }).bind(this));
          }

        }
        else
          this.error = true;
      }
    },
    mounted (){
      const token = localStorage.getItem('token');
    
      if (token)
      {
        axios.get('/user/me', {
          headers: {
            Authorization: token
        }}).then(res => {
          this.linkavatar = res.data.avatar;
          this.username = res.data.username;
          this.usernameEdit = this.username;
          this.intra_login = res.data.intra_login;
          this.status = res.data.status;
          this.id = res.data.id;
          this.twofactor = res.data.two_factor_authentication;
        })
        .catch(error => {
        });

        axios.get('/QR', {
          headers: {
            Authorization: token
        }}).then(res => {
          this.QRcode = res.data;
        })
        .catch(error => {
        });
      }

    }
  }
