
import axios from 'axios';

  export default {
    props: ['avatar'],
    data: () => ({
      dialog: false,
      error: false,
      roompassword: "",
      rooms: [
        ]
    }),
    methods: {
      trypassword: function(roomName)
      {
        const token = localStorage.getItem('token');
    
        if (token)
        {
          const data = {
            password: this.roompassword,
          };
          axios.post('/rooms/joinRoom/'+roomName, data, {
            headers: {
              Authorization: token
          }}).then((function(res) {
            this.rooms = this.rooms.filter(data => data.roomName !== roomName);
            this.$emit("Addroom", roomName);
            this.error = false;
            this.roompassword = '';
          }).bind(this))
          .catch(error => {
            this.error = true;
			console.log(error);
          });
        }
      },
      joinRoom: function (roomName)
      {
        const token = localStorage.getItem('token');
    
        if (token)
        {
          axios.post('/rooms/joinRoom/'+roomName, {}, {
            headers: {
              Authorization: token
          }}).then((function (res) {
            this.rooms = this.rooms.filter(data => data.roomName !== roomName);
            this.$emit("Addroom", roomName);
            this.error = false;
          }).bind(this))
          .catch(error => {
            this.error = true;
          });
          
        }
      }
    },
    mounted (){
      const token = localStorage.getItem('token');
    
      if (token)
      {
        axios.get('/rooms/roomsList', {
          headers: {
            Authorization: token
        }}).then((function(res) {
          this.rooms = res.data;
          axios.get('/rooms/findUserRooms', {
            headers: {
              Authorization: token
          }}).then((function(res) {
            let tmprooms = res.data;
            
            
            
            this.rooms = this.rooms.filter((function ( el )
            {
              for(let i = 0; i < tmprooms.length; i++)
              {
                if (tmprooms[i].roomName == el.roomName)
                {
                  return false;
                }
              }
              return true;
            }));

          }).bind(this))
          .catch(error => {

          });
        }).bind(this))
        .catch(error => {

        });
          
      }
    }
  }
