
import axios from 'axios';

  export default {
    data: () => ({
      dialog: false,
      error: false,
      roomname: "",
      roompassword: "",
    }),
    methods: {
      creatroom: function (){

        const token = localStorage.getItem('token');
		var format = /[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/;

        if (token && this.roomname && this.roomname.length > 0 && !format.test(this.roomname))
        {
          let st = 0;
          if (this.roompassword != '')
            st = 1;
          const data = {
            roomName: this.roomname,
            password: this.roompassword,
            state: st
          };
          axios.post('/rooms/create', data , {
              headers: {
                Authorization: token
              }
            }).then((function (res){
              this.$emit("Addroom", res.data.roomName);
              this.roomname = "";
              this.error = false;
              this.dialog = false;
              this.roompassword = "";
            }).bind(this))
            .catch(error => {
              this.error = true;
            });

        }
        
      },
    },
  }
