
import Vue from 'vue';
import axios from 'axios';

export default Vue.extend({
    data: () => ({
      drawer: null,
      verification: "",
      error: false
    }),
    methods: {
      verify: function()
      {
        const id = localStorage.getItem('id');
        if (id.length > 1)
        {
          const str = id.substring(1)
          if (/^[0-9]+$/.test(str) && /^[0-9]+$/.test(this.verification))
          {
            const data = {
              id: +str,
              number: +this.verification
            };
            axios.put('/2FA/verify', data,{}
            ).then((function (res) {
              localStorage.setItem("token", "Bearer " + res.data.token);
              this.error = false;
              this.$router.push({ path: '/Game' }).catch(() => {})
              this.$router.go(1);
            }).bind(this))
            .catch((function (err)  {
              this.error = true;

            }).bind(this));
          }
          else
            this.error = true;

        }
        
      }
    }
});
