
  import axios from 'axios';
  
  export default {
    mounted (){
      const token = localStorage.getItem('token');

      if (token)
      {
        axios.get('/get_history/'+this.id, {
          headers: {
            Authorization: token
          }
        }).then((function (res) {
          this.games = res.data;
        }).bind(this))
        .catch(error => {
        });
        axios.get('/get_achievm/'+this.id, {
          headers: {
            Authorization: token
          }
        }).then((function (res) {
          this.ach = res.data;
        }).bind(this))
        .catch(error => {
        });
      }
    },
    data: () => ({
      dialog: false,
      games: [],
      ach: [],
      uri: 'http://'+process.env.VUE_APP_HOSTIP+':'+process.env.VUE_APP_BACKPORT,
    }),
    props: ['avatar', 'username', 'id']
  }
